import React from 'react'
import Project from './Project'
import { data } from '../data'
function Projects() {
    return (
        <section id="two">
            <h2 style = {{textAlign: 'center'}}>Recent Work &amp; Projects</h2>
            <div class="row">
                {data.map((project) => {
                    return <Project key = {project.id}  {...project}/>
                })}
            </div>
        </section>
    )
}

export default Projects
